.content-label {
    font-size: 14px;
    height: 25px;
    line-height: 25px;
    border-radius: 3px;
    width: 50vw;
    background-color: #383E4E;
    color: #F3EFE7;
    text-align: center;
}

.group-buy {
    width: 100vw;
    height: 100px;
    background-image: url('https://hbimg.huabanimg.com/62d8eddcabb1e7c70e3fe8e611a9893ddb564537dcd9d-23xnQm_fw658');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}


.about-area {
    width: 100vw;
    height: 120px;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    box-sizing: border-box;
}

.about-ma-video,
.about-ma-article {
    height: 100%;
    width: 50%;
    border-radius: 3px;
    text-align: center;
    line-height: 40vw;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.about-ma-video {
    background-image: url('https://hbimg.huabanimg.com/57dec285a0c4b9bdf87cd76c7dd1628581623ad34ea46-kLYCT3_fw658');
}
.about-ma-article {
    background-image: url('https://hbimg.huabanimg.com/358d22fc53fc8d0d7c6a099d866c0ffd991f5b753b8f0-REUWzM_fw658');
}


.am-grid.am-grid-square .am-grid-item .am-grid-item-inner-content .am-grid-icon {
    margin-top: 9px;
    width: 38%!important;
}

