
.detail-img {
    width: 100vw;
    height: 150px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.detail-info-operate {
    background-color: #FFFFFF;
    font-size: 15px;
}
.describe {
    padding: 10px;
}

.price-amount {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
}
.yuan {
    font-size: 12px;
}
.am-list-content {
    font-size: 14px !important;
}



.detail-content {
    background-color: #FFFFFF;
    min-height: 50vh;
    padding: 10px;
}
.content-image {
    width: 100%;
    height: 260px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: 10px;
}
.content-text {
    font-size: 15px;
    line-height: 30px;
    padding: 10px;

}



.detail-order {
    height: 50px;
    width: 100vw;
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: #FFFFFF;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 0 10px;
    box-shadow: 0 0 3px #383E4E;
}
.order-info-dt {
    display: flex;
    align-items: center;
}


.order-icon {
    width: 20px;
    height: 20px;
    margin-right: 15px;
    display: inline-block;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('https://hbimg.huabanimg.com/60445943093130154843d9d6a6d5d42f60cdd1d7150d4-3VEj8M_fw658');
}
.order-total {
    display: inline-block;
    font-size: 15px;
}

.do-placeholder {
    height: 50px;
    width: 100vw;
}