.user-info {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: #383E4E;
    color: #F3EFE7;
    height: 150px;
    width: 100vw;
    padding: 0 10px;
}

.ui-head-img {
    height: 90px;
    width: 90px;
    border-radius: 100%;
    border: 4px solid #F3EFE7;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-right: 20px;
}

.ui-info {

}
.uii-username {
    font-weight: 700;
    max-width: 50vw;
    font-size: 20px;
    margin-bottom: 15px;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
}
.uii-otherinfo {

}

.uuioi-item {
    margin-right: 20px;
}
.am-card-header-extra,
.am-card-header {
    font-size: 14px !important;
}
.am-card-footer {
    padding: 6px 15px;
}




.order-list {
    padding: 0 10px;
}
.list-head {
    height: 40px;
    line-height: 55px;
}
.card-body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
    font-size: 14px;
}

.order-img {
    width: 80px;
    height: 80px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}


.order-info {
    font-size: 14px;
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.oi-right {
    color: #888;
}


.am-card-footer-extra {
	display: flex;
	justify-content: flex-end;
}
.gouse-right {
	margin-left: 10px;
}