/* tab */
.tab-area {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #dfdfdf;
}
.tabs {
    width: 25vw;
}
.tab-item {
    height: 60px;
    text-align: center;
    line-height: 60px;
    border-top: 1px solid #dfdfdf;
    font-size: 14px;
    background-color: #383E4E;
    color: #F3EFE7;
}
.tab-item.on {
    background: transparent;
    color: #383E4E;
    border-bottom: 1px solid #dfdfdf;
}
.tab-item:first-child {
    border: 0;
}


.tab-content {
    width: 75vw;
    box-sizing: border-box;
    padding: 0 10px;
}
.prod-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    border-top: 1px solid #dfdfdf;
}
.prod-item:first-child {
    border: 0;
}
.pi-img {
    height: 15vw;
    width: 15vw;
    border-radius: 100%;
}
.pi-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.pi-desc {
    margin-left: 10px;
    font-size: 14px;
    color: #383E4E;
}
.pi-price {
    font-size: 10px;
    color: #383E4E;
}
.pi-price label {
    font-size: 8px;
}